import React, { Component, Fragment } from "react";
import Highcharts from "highcharts";
import HighchartsReact from "highcharts-react-official";


class Scatter extends Component {
    state = {
        data:[
            {name:' ', y: this.props.nwrScore},
            {name:' ', y: this.props.srScore}
        ]
    }

    render() {
        const series2 = this.state.data;    //App.js   
        const options = {
            chart: {
                type: 'bar',
                width: 400,
                height: 250			// bar.    line chart .
            },
            title: {
                text: '',
                style:{
                    fontWeight:"bold",
                },
            },
            credits: {
                enabled: false
            },
            xAxis: {
                categories: [' ', ' '],
                labels:{
                    x: 2,
                    y: -35,
                    align: 'left',
                    style:{
                        fontWeight:'bold'
                    }
                }    

            },

            yAxis:{
                title: null,
                plotLines: [{
                    color: '#FF0000',
                    width: 2,
                    value: 70,
                    label: {text:'', rotation: 0, z:0}
                }]
            },

            legend: {
                enabled: false
            },
            tooltip: {
                enabled:false
            },
            plotOptions: {
                series: {
                    stacking: 'normal',
                    dataLabels: {
                        enabled: true,
                    },
                    bar:{
                        threshold:80
                    }
                }
            },
            series: [{ name: '' , data: series2 }]

        }
        return (
            <Fragment>
                 <div style={{padding:'50px 0'}}>
                    <HighchartsReact highcharts={Highcharts} options={options} />
                </div>
            </Fragment>
        );
    }
}
export default Scatter;